import { Button, Paper, Stack, Typography } from "@mui/material";
import React from "react";
import { getAssignTopPathFunc } from "../../utils/urlUtil";

const LoggedInHome = ({ profile }) => {
  window.top.history.replaceState(
    {},
    "LoggedInHome",
    `${window.top.location.protocol}//${window.top.location.host}/home`
  );

  const styles = {
    stack: {
      alignItems: "center"
    },
    paper: {
      padding: 2
    }
  };

  return (
    <div id="home-li">
      <Stack sx={styles.stack} spacing={2}>
        <Typography variant="h1" component="h2">
          Welcome to Sam's Page!
        </Typography>
        <Typography variant="h2">Welcome, {profile.name}</Typography>
        <Paper sx={styles.paper} data-testid="options-paper">
          <Stack sx={styles.stack}>
            <Typography>Here are some things you can do:</Typography>
            <Stack sx={styles.stack} direction="row" spacing={2}>
              <Button onClick={getAssignTopPathFunc("profile")}>
                View profile
              </Button>
              <Button onClick={getAssignTopPathFunc("grades?c=1")}>
                View classes
              </Button>
            </Stack>
          </Stack>
        </Paper>
      </Stack>
    </div>
  );
};

export default LoggedInHome;
