import React, { useState } from "react";
import PropTypes from "prop-types";
import { Buffer } from "buffer";
import {
  Alert,
  Button,
  FormControl,
  Snackbar,
  Stack,
  TextField
} from "@mui/material";
import { EncryptJWT } from "jose";
import axios from "axios";

import { assignTopPath } from "../../utils/urlUtil";

const SignIn = ({ secret }) => {
  window.top.history.replaceState(
    {},
    "SignIn",
    `${window.top.location.protocol}//${window.top.location.host}/signin`
  );

  const styles = {
    stack: {
      alignItems: "center",
      marginTop: "5%"
    },
    form: {
      width: "30%"
    },
    formStack: {
      width: "100%",
      alignItems: "stretch"
    }
  };
  const errorMessages = {
    noUsn: "No username was provided",
    noPwd: "No password was provided",
    noCred: "No username or password was provided",
    noAcc: "No account exists with that username and password",
    gen: "Error"
  };

  const [usnError, setUsnError] = useState(false);
  const [pwdError, setPwdError] = useState(false);
  const [accError, setAccError] = useState(false);
  const [showToast, setShowToast] = useState(false);
  const [toastMessage, setToastMessage] = useState("gen");

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const payload = {
        username: e.target["iusn"].value,
        password: Buffer.from(e.target["ipwd"].value)
          .toString("base64")
          .replaceAll("=", "")
      };
      if (!payload.username && !payload.password) throw new EvalError("none");
      if (!payload.username) throw new EvalError("usn");
      if (!payload.password) throw new EvalError("pwd");
      const token = await new EncryptJWT(payload)
        .setProtectedHeader({ alg: "RSA-OAEP", enc: "A256GCM" })
        .setIssuedAt()
        .setIssuer("sb:spa.base")
        .encrypt(await secret);
      const response = await axios.post(
        `${window.top.location.protocol}//${window.top.location.host}/auth/signIn`,
        {},
        {
          headers: {
            Authorization: `JWT ${token}`
          }
        }
      );
      if (response.status === 204) {
        setUsnError(false);
        setPwdError(false);
        setAccError(false);
        assignTopPath("home");
      }
    } catch (error) {
      switch (error.message) {
        case "usn":
          setUsnError(true);
          setPwdError(false);
          setAccError(false);
          setToastMessage("noUsn");
          break;
        case "pwd":
          setUsnError(false);
          setPwdError(true);
          setAccError(false);
          setToastMessage("noPwd");
          break;
        case "none":
          setUsnError(true);
          setPwdError(true);
          setAccError(false);
          setToastMessage("noCred");
          break;
        default:
          setUsnError(true);
          setPwdError(true);
          setAccError(false);
          setToastMessage("gen");
      }
      if (error.response && error.response.status === 401) {
        setUsnError(true);
        setPwdError(true);
        setAccError(true);
        setToastMessage("noAcc");
      }
      setShowToast(true);
    }
  };

  const handleCloseToast = (event, reason) => {
    if (reason === "clickaway") return;
    setShowToast(false);
  };

  return (
    <div id="sign-in">
      <Snackbar
        open={showToast}
        autoHideDuration={3000}
        onClose={handleCloseToast}
        anchorOrigin={{ horizontal: "right", vertical: "top" }}
      >
        <Alert onClose={handleCloseToast} severity="error">
          {errorMessages[toastMessage]}
        </Alert>
      </Snackbar>
      <Stack sx={styles.stack}>
        <form style={styles.form} name="sign-in-form" onSubmit={handleSubmit}>
          <FormControl sx={styles.formStack}>
            <Stack sx={styles.formStack} spacing={2}>
              <TextField
                label="Username"
                name="iusn"
                error={usnError}
                helperText={!accError && usnError ? "Username is required" : ""}
              />
              <TextField
                label="Password"
                type="password"
                name="ipwd"
                error={pwdError}
                helperText={!accError && pwdError ? "Password is required" : ""}
              />
              {accError && (
                <Alert severity="error">{errorMessages.noAcc}</Alert>
              )}
              <Button type="submit">Sign in</Button>
            </Stack>
          </FormControl>
        </form>
      </Stack>
    </div>
  );
};

SignIn.propTypes = {
  secret: PropTypes.object.isRequired
};

export default SignIn;
