import React, { useState } from "react";
import PropTypes from "prop-types";
import { Buffer } from "buffer";
import {
  Alert,
  Button,
  FormControl,
  Snackbar,
  Stack,
  TextField
} from "@mui/material";
import { EncryptJWT } from "jose";
import axios from "axios";

import { assignTopPath } from "../../utils/urlUtil";

const SignUp = ({ secret }) => {
  window.top.history.replaceState(
    {},
    "SignUp",
    `${window.top.location.protocol}//${window.top.location.host}/signup`
  );

  const styles = {
    stack: {
      alignItems: "center",
      marginTop: "5%"
    },
    form: {
      width: "30%"
    },
    formStack: {
      width: "100%",
      alignItems: "stretch"
    }
  };
  const errorMessages = {
    noUsn: "No username was provided",
    noPwd: "No password was provided",
    noNme: "No name was provided",
    noSome: "Not all credentials were provided",
    noCred: "No credentials were provided",
    yesAcc: "An account with that username and password already exists",
    gen: "Error"
  };

  const [usnError, setUsnError] = useState(false);
  const [nmeError, setNmeError] = useState(false);
  const [pwdError, setPwdError] = useState(false);
  const [accError, setAccError] = useState(false);
  const [showToast, setShowToast] = useState(false);
  const [toastMessage, setToastMessage] = useState("gen");

  const setErrors = (err) => {
    setUsnError(err);
    setNmeError(err);
    setPwdError(err);
    setAccError(err);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const payload = {
        username: e.target[0].value,
        name: e.target[2].value,
        password: Buffer.from(e.target[4].value)
          .toString("base64")
          .replaceAll("=", "")
      };
      let error = "";
      if (!payload.username) error += "u";
      if (!payload.name) error += "n";
      if (!payload.password) error += "p";
      if (error !== "") throw new EvalError(error);
      const token = await new EncryptJWT(payload)
        .setProtectedHeader({ alg: "RSA-OAEP", enc: "A256GCM" })
        .setIssuedAt()
        .setIssuer("sb:spa.base")
        .encrypt(await secret);
      const response = await axios.post(
        `${window.top.location.protocol}//${window.top.location.host}/auth/signup`,
        {},
        {
          headers: {
            Authorization: `JWT ${token}`
          }
        }
      );
      if (response.status === 204) {
        setErrors(false);
        assignTopPath("home");
      }
    } catch (error) {
      console.log(error);
      if (error.message && !error.response) {
        setAccError(false);
        setUsnError(error.message.includes("u"));
        setNmeError(error.message.includes("n"));
        setPwdError(error.message.includes("p"));

        if (error.message === "unp") {
          setToastMessage("noCred");
        }
        if (error.message.length === 2) {
          setToastMessage("noSome");
        }
        if (error.message.length === 1)
          switch (error.message) {
            case "u":
              setToastMessage("noUsn");
              break;
            case "n":
              setToastMessage("noNme");
              break;
            case "p":
              setToastMessage("noPwd");
              break;
            default:
              setToastMessage("gen");
          }
      }
      if (error.response && error.response.status === 401) {
        setErrors(true);
        setToastMessage("yesAcc");
      }
      setShowToast(true);
    }
  };

  const handleCloseToast = (event, reason) => {
    if (reason === "clickaway") return;
    setShowToast(false);
  };

  return (
    <div id="sign-up">
      <Snackbar
        open={showToast}
        autoHideDuration={3000}
        onClose={handleCloseToast}
        anchorOrigin={{ horizontal: "right", vertical: "top" }}
      >
        <Alert onClose={handleCloseToast} severity="error">
          {errorMessages[toastMessage]}
        </Alert>
      </Snackbar>
      <Stack sx={styles.stack}>
        <form style={styles.form} name="sign-up-form" onSubmit={handleSubmit}>
          <FormControl sx={styles.formStack}>
            <Stack sx={styles.formStack} spacing={2}>
              <TextField
                label="Username"
                name="uusn"
                error={usnError}
                helperText={!accError && usnError ? "Username is required" : ""}
              />
              <TextField
                label="Name"
                name="unme"
                error={nmeError}
                helperText={!accError && nmeError ? "Name is required" : ""}
              />
              <TextField
                label="Password"
                type="password"
                name="upwd"
                error={pwdError}
                helperText={!accError && pwdError ? "Password is required" : ""}
              />
              {accError && (
                <Alert severity="error">{errorMessages.yesAcc}</Alert>
              )}
              <Button type="submit">Sign up</Button>
            </Stack>
          </FormControl>
        </form>
      </Stack>
    </div>
  );
};

SignUp.propTypes = {
  secret: PropTypes.object.isRequired
};

export default SignUp;
