import "./App.css";
import Root from "./Components/Root";
import { BrowserRouter as Router } from "react-router-dom";
import PropTypes from "prop-types";

function App({ secret }) {
  return (
    <Router>
      <Root secret={secret} />
    </Router>
  );
}

App.propTypes = {
  secret: PropTypes.object.isRequired
};

export default App;
