import React from "react";
import { Stack, Typography } from "@mui/material";

const Profile = ({ profile }) => {
  const styles = {
    stack: {
      alignItems: "center"
    }
  };
  if (profile === null) {
    window.top.location.assign(
      `${window.top.location.protocol}//${window.top.location.host}/signin`
    );
    return null;
  }
  window.top.history.replaceState(
    {},
    "Profile",
    `${window.top.location.protocol}//${window.top.location.host}/profile`
  );

  return (
    <div id="profile">
      <Stack sx={styles.stack} spacing={2}>
        <Typography variant="h1" component="h2">
          Profile information
        </Typography>
        <Typography variant="h2" component="p">
          Your username is {profile.username}
        </Typography>
      </Stack>
    </div>
  );
};

export default Profile;
